<template>
  <div class="commit">
    <ul>
    <li v-for="record in commits">
      <a :href="record.html_url" target="_blank" class="commit">{{ record.sha.slice(0, 7) }}</a>
      - <span class="message">{{ record.commit.message | truncate }}</span><br>
      by <span class="author"><a :href="record.author.html_url" target="_blank">{{ record.commit.author.name }}</a></span>
      at <span class="date">{{ record.commit.author.date | formatDate }}</span>
    </li>
  </ul>
  </div>
</template>

<script>
var apiURL = 'http://api.swift.gg/v1/article/categoryList'
// var apiURL = 'https://api.github.com/repos/vuejs/vue/commits?per_page=3&sha=master'

export default {
  data () {
    return {
      branches: ['master', 'dev'],
      currentBranch: 'master',
      commits: null
    }
  },
  created: function () {
    this.fetchData()
  },
  methods: {
    fetchData: function () {
      // var http = new Resource()
      this.$http.get(apiURL).then((response) => {
        // this.$set('commits', response.json())
        console.log(response)
        this.commits = response.body.data
      })
      // var xhr = new XMLHttpRequest()
      // var self = this
      // xhr.open('GET', apiURL + self.currentBranch)
      // xhr.onload = function () {
      //   self.commits = JSON.parse(xhr.responseText)
      //   console.log(self.commits[0].html_url)
      // }
      // xhr.send()
    }
  }
}
</script>

